import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'

import QuestionAnswerList from '@/view/comment/comment-question-answer-list.vue'; //问答列表

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/question-answer-list.html',
  routes: [
    {
      path: '/',
      name: 'QuestionAnswerList',
      component: QuestionAnswerList
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


